<template>
    <!-- <div class="aniBox" @click="play()"> -->
    <lottie :options="defaultOptions" :height="400" :width="400" v-on:animCreated="handleAnimation" style='' />
    <!-- </div> -->
</template>

<script>

import animationData from '../../assets/star.json';
export default {
    name: 'lottieStart',
    data() {
        return {
            defaultOptions: {
                animationData: animationData,
                autoplay: false,
                loop: false,
            },
            animationSpeed: 1,
            anim: {
            }
        }
    },
    methods: {
        handleAnimation(anim) {
            this.anim = anim;
            console.log(anim); // lottie 
            this.anim.goToAndStop(40, true)// 
        },
        stop: function () {//
            this.anim.stop();
        },

        play: function () {
            this.anim.goToAndStop(0)// 
            this.anim.play()
            // this.anim.setDirection(-1)
        },

        pause: function () {
            this.anim.pause();
        },

        setDirection() {//
            this.anim.setDirection(-1)
            this.anim.play()
            // 1
            // -1
        },
        unsetDirection() {//
            this.anim.goToAndStop(0)// 
            this.anim.setDirection(1)
            this.anim.play()
            // 1
            // -1
        },
        gotoandStop() {
            this.anim.goToAndStop(1000, true)
            // ,
            // .true
            // :false
            // Anim.goToAndStop(value, isFrame); // /isFrame(false)value()
            // Anim.goToAndPlay(value, isFrame); // /
            // Anim.goToAndStop(30, true); // 30
            // Anim.goToAndPlay(300); // 300300
        },
        lastFram() {
            this.anim.goToAndStop(60, true)
        }
    },
    mounted() {
        this.anim.goToAndStop(0, true)// 
        this.anim.addEventListener('complete', () => {
            console.log('')
        })
        this.anim.addEventListener('data_ready', () => {
            console.log('animation data has loaded');

        })
    }
}
</script>

<style lang="" scoped>
.aniBox {
    height: 100%;
    position: fixed;
    width: 100%;
    z-index: 100000;
    background: white;
    overflow-y: hidden;
    bottom: 0;
    left: 0;
}
.animate {
}
</style>


